import socketIO from 'socket.io';
const io = socketIO();

export default io;

io.on('connection', socket => {

  console.log(socket.request.url);
  console.log(socket.handshake.query);

  let {client, group} = socket.request.url

  socket.join(group);

  socket.on(data => {
    console.log(data);
  });

});


